<!--
 * @Author: linxu
 * @Date: 2021-12-20 13:53:25
 * @LastEditors: linxu
 * @LastEditTime: 2022-02-22 13:42:46
 * @Description: 员工监控大屏
-->
<template>
  <div class="employee-monitoring-screen monitoring-full-screen">
    <div
      class="ee-screen-content"
      :style="{
        transform: `scale(${scale}) translate(-50%, 0%)`,
        width: `${width}px`,
        height: `${height}px`
      }"
    >
      <!-- 员工监控大屏 -->
      <el-header class="ee-screen-header">
        <div class="screen-header-left">
          <svg-icon icon-class="Icon-ee-header-left1"></svg-icon>
          <el-row type="flex">
            <el-col>
              <el-button class="city-button">
                <i class="el-icon-location-information"> </i>
                成都市
              </el-button>
            </el-col>
            <el-col>
              <div class="nowTime">
                <p>{{ dateStr }}</p>
                <p>{{ timeStr }}</p>
              </div>
            </el-col>
          </el-row>
        </div>
        <div class="screen-header-center">
          <el-row type="flex">
            <el-col>
              <h2>智能装备动力服务租赁平台</h2>
            </el-col>
          </el-row>
        </div>
        <div class="screen-header-right">
          <svg-icon icon-class="Icon-ee-header-right1"></svg-icon>
          <el-row type="flex" justify="center">
            <el-col style="margin-left: 16px">
              <el-row type="flex" align="middle">
                <el-avatar :src="HeadImgUrl"> </el-avatar>
                &nbsp;&nbsp;{{ this.$wsCache.get('userInfo').displayName }}，您好！
              </el-row>
            </el-col>
            <el-col>
              <el-row class="btns">
                <el-col>
                  <el-button class="goto-system-btn enter" @click="pushManage">
                    管理系统
                  </el-button>
                </el-col>
                <el-col>
                  <el-button class="goto-system-btn logout" @click="logout"> 退出登录 </el-button>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </div>
      </el-header>
      <el-main class="ee-screen-main">
        <ScreenMainLeft class="screen-main-left"></ScreenMainLeft>
        <ScreenMainCenter class="screen-main-center"></ScreenMainCenter>
        <ScreenMainRight class="screen-main-right"></ScreenMainRight>
      </el-main>
    </div>
  </div>
</template>

<script>
import ScreenMainLeft from './components/ScreenMainLeft'
import ScreenMainCenter from './components/ScreenMainCenter'
import ScreenMainRight from './components/ScreenMainRight'
import _ from 'lodash'

// 引入公共方法
import { getWeekTime } from '@/utils/common'

export default {
  name: 'EmployeeMonitoringScreen',
  components: {
    ScreenMainLeft,
    ScreenMainCenter,
    ScreenMainRight
  },
  data() {
    return {
      width: 1920,
      height: 1080,
      scale: 1,

      HeadImgUrl: '',
      dateStr: '',
      timeStr: ''
    }
  },

  created() {
    this.setScale()
  },
  beforeMount() {
    window.addEventListener('resize', this.setScale)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.setScale)
  },
  mounted() {
    this.getAccount()
    this.renderTime()
  },
  methods: {
    getScale() {
      // 固定好16:9的宽高比，计算出最合适的缩放比，宽高比可根据需要自行更改
      const { width = 1920, height = 1080 } = this.$data
      let ww = window.innerWidth / width
      let wh = window.innerHeight / height
      return ww < wh ? ww : wh
    },
    setScale: _.debounce(function () {
      // 获取到缩放比，并设置
      this.scale = this.getScale()
    }, 300),

    getAccount() {
      this.$api.account.findAccount().then((res) => {
        if (res.IsSuccess) {
          const data = res.Data
          this.HeadImgUrl = data.HeadImgUrl
        }
      })
    },
    pushManage() {
      this.$router.push('/syshomePage/home')
    },
    async logout() {
      const res = await this.$api.login.logout()
      if (res.IsSuccess) {
        this.$wsCache.delete('loginFlag')
        this.$wsCache.delete('userInfo')
        this.$wsCache.delete('selectCrew')
        this.$router.push('/login')
      }
    },
    renderTime() {
      const format = `YYYY年M月D日 星期 HH:mm`
      let str = getWeekTime(format)
      this.dateStr = str.split(' ')[0]
      this.timeStr = `${str.split(' ')[1]} ${str.split(' ')[2]}`
      setTimeout(() => {
        this.renderTime()
      }, 60 * 1000)
    }
  }
}
</script>

<style lang="less" scoped>
// 页面框架布局
.employee-monitoring-screen {
  height: 100%;
  background: linear-gradient(
    180deg,
    rgba(10, 22, 46, 1) 0%,
    rgba(16, 36, 71, 1) 40%,
    rgba(16, 36, 71, 1) 61%,
    rgba(8, 16, 37, 1) 100%
  );
  color: #fff;
  .ee-screen-content {
    transform-origin: 0 0;
    position: absolute;
    left: 50%;
    top: 0;
    transition: 0.3s;
  }
  .ee-screen-header {
    padding: 0;
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
    // border: 1px solid white;
    > div {
      width: 25%;
      height: 100%;
      &.screen-header-center {
        width: 50%;
      }
    }
  }
  .ee-screen-main {
    display: flex;
    padding: 24px 16px 16px;
    height: calc(100% - 60px);
    box-sizing: border-box;
    justify-content: space-between;
    > div {
      position: relative;
      width: 25%;
      height: 100%;
      box-sizing: border-box;
      // border: 1px solid white;
      &.screen-main-center {
        width: 50%;
        margin: 0 16px;
      }
    }
  }
}
.ee-screen-header {
  color: #1acdfd;
  .screen-header-left {
    position: relative;
    padding-left: 16px;
    box-sizing: border-box;
    background: linear-gradient(to top, #154964 8%, #0a1b33 50%, transparent 100%) left top
      no-repeat;
    .svg-icon {
      position: absolute;
      right: -65px;
      width: 55%;
      height: 60px;
      z-index: 1;
      opacity: 0.3;
    }
    .city-button {
      padding: 8px 0;
      border: 0;
      background-color: transparent;
      color: #1acdfd;
      font-size: 1.3rem;
      line-height: 44px;
    }
    .nowTime {
      text-align: right;
      margin-right: 16px;
      padding: 6px 0;
      p {
        line-height: 24px;
      }
    }
  }
  .screen-header-center {
    h2 {
      margin: 0 16px;
      font-size: 40px;
      text-align: center;
      line-height: 80px;
      background-image: linear-gradient(#0a1b33, #154964);
      border-radius: 0 0 68px 68px;
    }
  }
  .screen-header-right {
    position: relative;
    padding-right: 16px;
    box-sizing: border-box;
    background: linear-gradient(to top, #154964 8%, #0a1b33 50%, transparent 100%) left top
      no-repeat;
    .svg-icon {
      position: absolute;
      left: -65px;
      width: 55%;
      height: 60px;
      z-index: 1;
      opacity: 0.3;
    }
    .el-row {
      height: 100%;
    }
    .btns {
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
    .goto-system-btn {
      margin-left: 16px;
      background: transparent;
      color: #fff;
      border-color: #00ccff;
      &:hover {
        background-color: rgba(26, 205, 253, 0.8);
        -webkit-animation: oto-system-btn 0.5s linear 1;
        animation: oto-system-btn 0.5s linear 1;
      }
    }
  }
  .icon-center {
    width: 100%;
    height: 80px;
  }
}

.left-content,
.center-content,
.right-content {
  display: flex;
  flex-direction: column;
  /deep/.content-item {
    width: 100%;
    padding: 6px;
    box-sizing: border-box;
    background: linear-gradient(to left, rgb(0, 204, 255), rgb(0, 204, 255)) left top no-repeat,
      linear-gradient(to bottom, rgb(0, 204, 255), rgb(0, 204, 255)) left top no-repeat,
      linear-gradient(to left, rgb(0, 204, 255), rgb(0, 204, 255)) right top no-repeat,
      linear-gradient(to bottom, rgb(0, 204, 255), rgb(0, 204, 255)) right top no-repeat,
      linear-gradient(to left, rgb(0, 204, 255), rgb(0, 204, 255)) left bottom no-repeat,
      linear-gradient(to bottom, rgb(0, 204, 255), rgb(0, 204, 255)) left bottom no-repeat,
      linear-gradient(to left, rgb(0, 204, 255), rgb(0, 204, 255)) right bottom no-repeat,
      linear-gradient(to bottom, rgb(0, 204, 255), rgb(0, 204, 255)) right bottom no-repeat;
    /*设置大小*/
    background-size: 1px 20px, 20px 1px, 1px 20px, 20px 1px, 1px 20px, 20px 1px, 1px 20px, 20px 1px;
  }
  // 项目标题 样式
  /deep/ .project-title {
    border-left: 4px solid #20fb8e;
    padding-left: 8px;
    font-size: 20px;
    line-height: 20px;
  }
}
</style>



