<template>
  <div style="line-height: normal;">
    <table>
      <tr>
        <td :colspan="treeData.children ? treeData.children.length * 2 : 1"
            :class="{parentLevel: treeData.children, extend: treeData.children}">
          <div :class="{node: true}">
            <div class="person">
              <div class="name"
                   :class="treeData.level==0?'level':''">
                <p>{{treeData.name}}</p>
                <p class="type"
                   v-if="treeData.type">{{treeData.type}}</p>
              </div>
            </div>
          </div>
        </td>
      </tr>
      <tr v-if="treeData.data">
        <td>
          <div class="node crew-detail">
            <el-row class="crew-num"
                    type="flex">
              <!--  // 0 所有 2报警 3警告 4运行 5 停机 6离线 -->
              <el-col :span="4"
                      class="color_type_4"><span></span><em>0</em></el-col>
              <el-col :span="4"
                      class="color_type_2"><span></span><em>0</em></el-col>
              <el-col :span="4"
                      class="color_type_3"><span></span><em>0</em></el-col>
              <el-col :span="4"
                      class="color_type_5"><span></span><em>0</em></el-col>
              <el-col :span="4"
                      class="color_type_6"><span></span><em>0</em></el-col>
              <el-col :span="4"
                      class="color_type_0"><span></span><em>0</em></el-col>
            </el-row>
            <div class="crewinfo-items">
              <el-row class="crewinfo-item"
                      v-for="i in Math.ceil(Math.random()*10)"
                      :key="i"
                      type="flex">
                <el-col :span="4"
                        :class="`color_type_${ crewStatus(i) }`"><span></span><em>运行</em></el-col>
                <el-col :span="20">
                  <p>FPSS8610110004</p>
                  <p>单机组</p>
                  <p class="overText">山东省潍坊市奎文区梨园街fasdadasdwqdddddd</p>
                </el-col>
              </el-row>
            </div>
          </div>
        </td>
      </tr>
      <tr v-if="treeData.children && treeData.children.length">
        <td v-for="(children, index) in treeData.children"
            :key="index"
            colspan="2"
            class="childLevel">
          <TreeChart :json="children" />
        </td>
      </tr>
    </table>
  </div>
</template>

<script>

export default {
  name: 'TreeChart',
  inject: ['reload'],
  props: ['json'],
  data () {
    return {
      treeData: {}
    }
  },
  watch: {
    json: {
      handler: function (Props) {
        this.treeData = Props
      },
      immediate: true,
      deep: true
    }
  },
  computed: {
    crewStatus () {
      return function (index) {
        return [4, 2, 3, 5, 6, 0][Math.floor(Math.random() * 6)]
      }
    }
  },
  created () { },
  methods: {
    getdata: function (val) {
      this.$emit('func', val)
    }
  }
}
</script>
<style lang="less" scoped>
@import "~@/styles/variables.less";
table {
  width: 100%;
  margin: 0 auto;
  border-collapse: separate !important;
  border-spacing: 0 !important;
  td {
    position: relative;
    vertical-align: top;
    text-align: center;
    &.parentLevel {
      padding: 0 0 26px 0;
    }
    &:not(:last-child) {
      padding-right: 24px;
    }
  }
}

.extend::after {
  content: "";
  position: absolute;
  right: 50%;
  bottom: 14px;
  height: 13px;
  border-left: 1px solid #0099ff;
  /* transform: translate3d(-1px, 0, 0) */
}

.childLevel::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translate(-12px, 0);
  /* bottom: 100%; */
  top: -15px;
  height: 15px;
  border-left: 1px solid #0099ff;
  // transform: translate3d(-1px, 0, 0);
}

.childLevel::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: -16px;
  border-top: 1px solid #0099ff;
}

.childLevel:first-child:before,
.childLevel:last-child:before {
  display: none;
}

.childLevel:first-child:after {
  left: 50%;
  height: 15px;
  border: 1px solid;
  border-color: #0099ff transparent transparent #0099ff;
  border-radius: 6px 0 0 0;
  // transform: translate3d(1px, 0, 0);
}

.childLevel:last-child:after {
  right: 50%;
  height: 15px;
  border: 1px solid;
  border-color: #0099ff #0099ff transparent transparent;
  border-radius: 0 6px 0 0;
  // transform: translate3d(-1px, 0, 0);
}

.childLevel:first-child.childLevel:last-child::after {
  left: auto;
  border-radius: 0;
  border-color: transparent #0099ff transparent transparent;
  // transform: translate3d(1px, 0, 0);
}

.node {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  text-align: center;
}

.node .person {
  position: relative;
  display: inline-block;
  z-index: 2;
  .name {
    word-break: break-all;
    padding: 4px;
    line-height: 14px;
    border: 1px solid #0099ff;
    border-radius: 4px;
    cursor: pointer;
    height: 48px;
    width: 110px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &.level {
      width: 180px;
    }
    > p {
      line-height: 20px;
    }
  }
}

.crew-detail {
  width: 300px;
  height: 508px;
  margin: 26px 0 0 0;
  border: 1px solid #0099ff;
  border-radius: 4px;
  cursor: pointer;
  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: -26px;
    height: 26px;
    border-left: 1px solid #0099ff;
  }
  .crew-num {
    padding: 8px 0;
    border-bottom: 1px solid #0099ff;
    .el-col {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      line-height: 20px;
      span {
        width: 20px;
        height: 20px;
        margin-bottom: 4px;
      }
    }
  }
  .crewinfo-items {
    height: calc(100% - 62px);
    overflow-y: hidden;
    &:hover {
      overflow-y: auto;
    }
    .crewinfo-item {
      padding: 8px 0;
      border-bottom: 1px solid #0099ff;
      .el-col {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        span {
          width: 20px;
          height: 20px;
          margin-bottom: 8px;
        }
        p {
          width: 100%;
          padding: 0 8px;
          box-sizing: border-box;
          text-align: left;
          &.overText {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }
      }
    }
  }

  // 机组状态对应标签颜色设置
  // 运行
  .color_type_4 {
    color: @crew_status_4;
    span {
      background: @crew_status_4;
    }
  }
  // 报警
  .color_type_2 {
    color: @crew_status_2;
    span {
      background: @crew_status_2;
    }
  }
  // 警告
  .color_type_3 {
    color: @crew_status_3;
    span {
      background: @crew_status_3;
    }
  }
  // 停机
  .color_type_5 {
    color: @crew_status_5;
    span {
      background: @crew_status_5;
    }
  }
  // 离线
  .color_type_6 {
    color: @crew_status_6;
    span {
      background: @crew_status_6;
    }
  }
  // 全部
  .color_type_0 {
    color: @crew_status_0;
    span {
      background: @crew_status_0;
    }
  }
}
</style>



