 * @Autor: linxu
 * @Date: 2021-12-20 14:33:33
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-18 16:19:16
 * @Description: left
-->
<template>
  <div class="left-content">
    <div class="content-item content-item__top">
      <!-- 项目概况 -->
      <el-row class="mb16" type="flex" justify="space-between" align="middle">
        <el-col>
          <div class="project-title">项目概况</div>
        </el-col>
        <el-col class="year-select-box">
          <el-date-picker
            class="year-select-box"
            v-model="selYear"
            type="year"
            :clearable="false"
            placeholder="选择年"
            prefix-icon="el-icon-date"
            placement="bottom-start"
          >
          </el-date-picker>
        </el-col>
      </el-row>
      <el-row class="mb16 project-data-box" type="flex" justify="space-between">
        <el-col :span="6">
          <div>120</div>
          <p>项目总数</p>
        </el-col>
        <el-col :span="6">
          <div>120</div>
          <p>执行中</p>
        </el-col>
        <el-col :span="6">
          <div>120</div>
          <p>完工结算中</p>
        </el-col>
        <el-col :span="6">
          <div>120</div>
          <p>已完成</p>
        </el-col>
      </el-row>
      <!-- 项目执行进度 -->
      <div id="project-data-echart"></div>

      <!-- 机组使用状态 -->
      <el-row class="mb16" type="flex">
        <el-col>
          <div class="project-title">机组使用状态</div>
        </el-col>
      </el-row>
      <el-row class="crew-data-box" type="flex" justify="space-between">
        <el-col :span="6">
          <div>120</div>
          <p>空闲中</p>
        </el-col>
        <el-col :span="6">
          <div>120</div>
          <p>租赁中</p>
        </el-col>
        <el-col :span="6">
          <div>120</div>
          <p>检修中</p>
        </el-col>
        <el-col :span="6">
          <div>120</div>
          <p>待归还</p>
        </el-col>
      </el-row>
      <el-row class="carousel-box">
        <el-col>
          <el-carousel
            ref="carousel"
            height="45px"
            :interval="5000"
            direction="vertical"
            :autoplay="true"
            indicator-position="none"
          >
            <el-carousel-item v-for="item in 4" :key="item">
              <div class="crew-info">
                <i class="el-icon-warning"></i>{{ item }}
                即将到期机组：COMM012112010250【3天后到期】
              </div>
            </el-carousel-item>
          </el-carousel>
        </el-col>
      </el-row>
    </div>
    <div class="content-item content-item__bottom">
      <!-- 工程项目进行状态 -->
      <el-row class="mb16" type="flex">
        <el-col>
          <div class="project-title">工程项目进行状态</div>
        </el-col>
      </el-row>
      <div id="project-status-echart"></div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: '',
  data() {
    const projectDataEnum = [
      // '定期回款', '付全款执行中', '已结算未回款', '完工未结算', '15天内到期'
      {
        key: '1',
        value: '定期回款'
      },
      {
        key: '2',
        value: '付全款执行中'
      },
      {
        key: '3',
        value: '已结算未回款'
      },
      {
        key: '4',
        value: '完工未结算'
      },
      {
        key: '5',
        value: '15天内到期'
      }
    ]
    return {
      conditions: {},
      selYear: '',
      projectDataChart: null,
      projectDataOption: {
        title: {
          text: '执行中项目执行进度',
          textStyle: {
            color: '#fff',
            fontSize: 14,
            fontWeight: 'normal'
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          right: 0,
          itemWidth: 20,
          itemHeight: 16,
          formatter: ['{a|{name}}'].join('\n'),
          textStyle: {
            color: '#fff',
            height: 16,
            rich: {
              a: {
                verticalAlign: 'middle'
              }
            }
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'value',
          boundaryGap: [0, 0.01],
          axisLabel: {
            color: '#fff',
            fontSize: 13
          }
        },
        yAxis: {
          type: 'category',
          axisLine: {
            show: false
          },
          axisTick: {
            show: false,
            alignWithLabel: true
          },
          splitLine: {
            show: true,
            interval: 4
          },
          axisLabel: {
            color: '#fff',
            fontSize: 13
          },
          data: projectDataEnum.map((item) => item.value)
        },
        series: [
          {
            name: '项目数量',
            type: 'bar',
            data: []
          }
        ]
      },
      projectStatusChart: null,
      projectStatusOption: {
        title: {
          text: '进行中项目\n200个',
          textStyle: {
            color: '#fff',
            fontSize: 18,
            lineHeight: 20
          },
          x: 'center',
          y: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          bottom: '5%',
          left: '0',
          textStyle: {
            color: '#fff'
          }
        },
        color: ['#ffcfa8', '#8ec9f4', '#cdbfe8', '#3180c0', '#6bc1bd', '#6552d5', '#53a7be'],
        series: [
          {
            name: '工程项目进行状态',
            type: 'pie',
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            label: {
              show: false
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '14',
                fontWeight: 'normal',
                color: '#fff',
                formatter: '{b} \n {c}',
                lineHeight: 18
              },
              labelLine: {
                show: true,
                lineStyle: {
                  color: '#00ccff'
                }
              }
            },
            labelLine: {
              show: false
            },
            data: [
              { value: 1048, name: '备机' },
              { value: 735, name: '发货' },
              { value: 580, name: '安装' },
              { value: 484, name: '调试' },
              { value: 580, name: '维保策略执行' },
              { value: 484, name: '回收' },
              { value: 300, name: '检修存储' }
            ]
          },
          {
            // 边框的设置
            type: 'pie',
            radius: ['40%', '74%'],
            center: ['50%', '50%'],
            emphasis: {
              scale: false
            },
            itemStyle: {
              borderWidth: 20,
              color: 'rgba(255, 255, 255, 1)'
            },
            label: {
              show: false
            },
            tooltip: {
              show: false
            },
            data: [100],
            z: -1
          }
        ]
      }
    }
  },
  computed: {
    ...mapGetters(['screenDataConditions'])
  },
  watch: {
    screenDataConditions: {
      handler: function (newVal, oldVal) {
        this.conditions = newVal
        // this.selYear = newVal.year
        this.getPageData()
      },
      deep: true
    },
    selYear(newVal, oldVal) {
      this.conditions['year'] = this.$moment(newVal).format('YYYY')
      this.toggleScreenDataConditons(this.conditions)
    }
  },
  mounted() {
    // this.echartsInit()
    this.selYear = new Date().getFullYear() + ''
    this.resizefun = () => {
      this.projectDataChart.resize()
      this.projectStatusChart.resize()
    }
    window.addEventListener('resize', this.resizefun)
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.resizefun)
    this.resizefun = null
  },
  methods: {
    ...mapActions(['toggleScreenDataConditons']),
    // 查询数据
    getPageData() {
      // 渲染图表
      this.echartsInit()
    },
    echartsInit() {
      // 项目执行进度----柱状图
      const barChartDom = document.getElementById('project-data-echart')
      const myBarChart = this.$echarts.init(barChartDom, null, {
        renderer: 'svg'
      })
      this.projectDataOption.series[0].data = [18, 234, 29, 104, 63]
      this.projectDataChart = myBarChart
      myBarChart.setOption(this.projectDataOption)

      // 工程项目进行状态----饼图
      const pieChartDom = document.getElementById('project-status-echart')
      const myPieChart = this.$echarts.init(pieChartDom, null, {
        renderer: 'svg'
      })
      this.projectStatusChart = myPieChart
      myPieChart.setOption(this.projectStatusOption)
    }
  }
}
</script>

<style lang="less" scoped>
.left-content {
  .content-item {
    padding: 12px 20px;
    &.content-item__top {
      height: 65%;
      margin-bottom: 16px;
    }
    &.content-item__bottom {
      flex: 1;
    }
  }
}
.content-item__top {
  > .el-row.mb16 {
    margin-bottom: 16px;
  }
  .year-select-box {
    width: 90px;
    /deep/.el-date-editor {
      border: 1px solid rgba(88, 193, 190, 1);
      .el-input__inner {
        color: #fff;
        background: transparent;
        border: 0;
        padding-left: 15px;
      }
      .el-input__prefix {
        color: rgba(88, 193, 190, 1);
        left: initial;
        right: 5px;
        font-size: 16px;
      }
    }
  }
  .project-data-box,
  .crew-data-box {
    border: 1px solid rgba(0, 204, 255, 1);
    border-radius: 4px;
    .el-col {
      line-height: 28px;
      text-align: center;
      padding: 10px 0;
      &:not(:last-child) {
        border-right: 1px solid rgba(0, 204, 255, 1);
      }
      div {
        font-size: 28px;
        margin-bottom: 8px;
      }
    }
  }
  .crew-data-box {
    border-radius: 4px 4px 0 0;
  }
  .carousel-box {
    border: 1px solid rgba(0, 204, 255, 1);
    border-top: 0;
    border-radius: 0 0 4px 4px;
    .crew-info {
      padding: 0 16px;
      line-height: 45px;
      .el-icon-warning {
        font-size: 16px;
        margin-right: 8px;
        color: #20fb8e;
      }
    }
  }
  #project-data-echart {
    height: 270px;
    margin-bottom: 16px;
  }
}
.content-item__bottom {
  display: flex;
  flex-direction: column;
  #project-status-echart {
    flex: 1;
  }
}
</style>



