<!--
 * @Autor: linxu
 * @Date: 2021-12-20 14:33:33
 * @LastEditors: linxu
 * @LastEditTime: 2022-02-22 09:19:03
 * @Description: right
-->
<template>
  <div class="right-content">
    <div class="content-item content-item__top">
      <!-- 项目架构 -->
      <el-row class="mb16" type="flex" justify="space-between">
        <el-col :span="12">
          <div class="project-title">项目架构</div>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <div class="framework-detail-btn" @click="showFrameworkDetail">架构详情>></div>
        </el-col>
      </el-row>
      <div class="project-framework-box">
        <frameworkTree :json="treeData" />
      </div>
      <!-- 机组类型 -->
      <el-row class="mb16" type="flex">
        <el-col>
          <div class="project-title">机组类型</div>
        </el-col>
      </el-row>
      <div id="crew-type-echart"></div>
    </div>
    <div class="content-item content-item__bottom">
      <!-- 工单概况 -->
      <el-row class="mb8" type="flex">
        <el-col>
          <div class="project-title">工单概况</div>
        </el-col>
      </el-row>
      <el-row class="mb16 workorder-overview-box" type="flex" justify="space-between">
        <el-col :span="8">
          <div>120</div>
          <p>未受理工单数</p>
        </el-col>
        <el-col :span="8">
          <div>120</div>
          <p>进行中已受理工单</p>
        </el-col>
        <el-col :span="8">
          <div>120</div>
          <p>线上/线下完成</p>
        </el-col>
      </el-row>
      <!-- 进行中工单处理进度 -->
      <el-row class="mb8" type="flex">
        <el-col>
          <div class="project-title">进行中工单处理进度</div>
        </el-col>
      </el-row>
      <el-row class="mb16 workorder-progress-box" type="flex" justify="space-between">
        <el-col :span="8">
          <div>120</div>
          <p>未受理工单数</p>
        </el-col>
        <el-col :span="8">
          <div>120</div>
          <p>进行中已受理工单</p>
        </el-col>
        <el-col :span="8">
          <div>120</div>
          <p>线上/线下完成</p>
        </el-col>
      </el-row>

      <el-row class="workorder-data-box" type="flex" justify="space-between">
        <el-col :span="12" class="flexbox">
          <el-row class="mb8" type="flex">
            <el-col>
              <div class="project-title">报修渠道</div>
            </el-col>
          </el-row>
          <div id="repair-channel-echart"></div>
        </el-col>
        <el-col :span="12" class="flexbox">
          <el-row class="mb8" type="flex">
            <el-col>
              <div class="project-title">满意度评价</div>
            </el-col>
          </el-row>
          <div id="satisfaction-evaluation-echart"></div>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      title="架构详情"
      class="framework-detail-dialog"
      :center="true"
      :visible.sync="dialogVisible"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="dialogVisible = false"
    >
      <div class="framework-detail-box">
        <frameworkDetailTree v-if="dialogVisible" :json="treeData"></frameworkDetailTree>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import frameworkTree from './frameworkTree'
import frameworkDetailTree from './frameworkDetailTree'
import { mapGetters } from 'vuex'
export default {
  name: '',
  components: {
    frameworkTree,
    frameworkDetailTree
  },
  data() {
    return {
      conditions: {},
      crewTypeChart: null,
      crewTypeOption: {
        title: {
          text: '机组总数\n200个',
          textStyle: {
            color: '#fff',
            fontSize: 16,
            lineHeight: 20
          },
          x: 'center',
          y: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          show: false
        },
        color: ['#ffcfa8', '#8ec9f4', '#cdbfe8'],
        series: [
          {
            name: '机组类型',
            type: 'pie',
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            emphasis: {
              label: {
                show: true,
                fontSize: '14',
                fontWeight: 'normal',
                color: '#fff',
                formatter: '{b} \n {c}'
              }
            },
            label: {
              color: '#fff',
              formatter: '{b} \n {c}',
              lineHeight: 18
            },
            labelLine: {
              lineStyle: {
                color: '#00ccff'
              }
            },
            data: [
              { value: 1048, name: '自有项目' },
              { value: 580, name: '代运维机组' },
              { value: 735, name: '代租赁机组' }
            ]
          },
          {
            // 边框的设置
            type: 'pie',
            radius: ['40%', '75%'],
            center: ['50%', '50%'],
            emphasis: {
              scale: false
            },
            itemStyle: {
              borderWidth: 20,
              color: 'rgba(255, 255, 255, 1)'
            },
            label: {
              show: false
            },
            tooltip: {
              show: false
            },
            data: [100],
            z: -1
          }
        ]
      },
      repairChannelChart: null,
      repairChannelOption: {
        title: {
          text: '总报修单数 200',
          textStyle: {
            color: '#1acdfd',
            fontSize: 16
          },
          top: '5px',
          x: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          show: false
        },
        color: ['#ffcfa8', '#8ec9f4', '#caf982', '#cdbfe8'], // 自定义颜色
        series: [
          {
            name: '报修渠道',
            type: 'pie',
            radius: ['30%', '50%'],
            top: '10%',
            avoidLabelOverlap: false,
            emphasis: {
              label: {
                show: true,
                fontSize: '14',
                fontWeight: 'normal',
                color: '#fff',
                formatter: '{b} \n {c}'
              }
            },
            label: {
              color: '#fff',
              formatter: '{b} \n {c}',
              lineHeight: 18
            },
            labelLine: {
              lineStyle: {
                color: '#00ccff'
              }
              // length: 5,
              // length2: 8
            },
            data: [
              { value: 1048, name: '电话报修' },
              { value: 735, name: '一键报修' },
              { value: 580, name: '智能装备报警' },
              { value: 484, name: '扫码报修' }
            ]
          },
          {
            // 边框的设置
            type: 'pie',
            radius: ['30%', '55%'],
            top: '10%',
            // hoverAnimation: false,
            emphasis: {
              scale: false
            },
            itemStyle: {
              borderWidth: 20,
              color: 'rgba(255, 255, 255, 1)'
            },
            label: {
              show: false
            },
            tooltip: {
              show: false
            },
            data: [100],
            z: -1
          }
        ]
      },
      satisfactionEvaluationChart: null,
      satisfactionEvaluationOption: {
        title: {
          text: '评价总数 200',
          textStyle: {
            color: '#1acdfd',
            fontSize: 16
          },
          top: '5px',
          x: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          show: false
        },
        color: ['#ec808d', '#ffcfa8'],
        series: [
          {
            name: '满意度评价',
            type: 'pie',
            radius: ['30%', '50%'],
            top: '10%',
            avoidLabelOverlap: false,
            emphasis: {
              label: {
                show: true,
                fontSize: '14',
                fontWeight: 'normal',
                color: '#fff',
                formatter: '{b} \n {c}'
              }
            },
            label: {
              color: '#fff',
              formatter: '{b} \n {c}',
              lineHeight: 18
            },
            labelLine: {
              lineStyle: {
                color: '#00ccff'
              }
            },
            data: [
              { value: 300, name: '满意' },
              { value: 90, name: '不满意' }
            ]
          },
          {
            // 边框的设置
            type: 'pie',
            radius: ['30%', '55%'],
            top: '10%',
            // hoverAnimation: false,
            emphasis: {
              scale: false
            },
            itemStyle: {
              borderWidth: 20,
              color: 'rgba(255, 255, 255, 1)'
            },
            label: {
              show: false
            },
            tooltip: {
              show: false
            },
            data: [100],
            z: -1
          }
        ]
      },

      treeData: {
        id: 0,
        name: '智能装备动力服务租赁平台',
        level: 0,
        children: [
          {
            id: 1,
            name: '中国农业银行项目',
            level: 1,
            children: [
              {
                id: 1001,
                name: '23121台机组',
                type: '单机项目',
                level: 2,
                data: []
              }
            ]
          },
          {
            id: 2,
            name: 'fuchuang-2',
            level: 1,
            children: [
              {
                id: 2001,
                name: '2台机组',
                type: '单机项目',
                level: 2,
                data: []
              }
            ]
          },
          {
            id: 3,
            name: 'fuchuang-3',
            level: 1,
            children: [
              {
                id: 3001,
                name: 'fuchuang-3-1',
                level: 2,
                data: []
              }
            ]
          },
          {
            id: 4,
            name: 'fuchuang-4',
            level: 1,
            children: [
              {
                id: 4001,
                name: 'fuchuang-4-1',
                level: 2,
                data: []
              }
            ]
          },
          {
            id: 5,
            name: 'fuchuang-5',
            level: 1,
            children: [
              {
                id: 5001,
                name: 'fuchuang-5-1',
                level: 2,
                data: []
              }
            ]
          }
        ]
      },
      dialogVisible: false,
      resizefun: null
    }
  },
  computed: {
    ...mapGetters(['screenDataConditions'])
  },
  watch: {
    screenDataConditions: {
      handler: function (newVal, oldVal) {
        this.conditions = newVal
        this.getPageData()
      },
      deep: true
    }
  },
  mounted() {
    this.resizefun = () => {
      this.crewTypeChart.resize()
      this.repairChannelChart.resize()
      this.satisfactionEvaluationChart.resize()
    }
    window.addEventListener('resize', this.resizefun)
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.resizefun)
    this.resizefun = null
  },
  methods: {
    // 页面数据查询
    getPageData() {
      this.echartsInit()
    },
    echartsInit() {
      // 机组类型----饼图
      const pieChartDom0 = document.getElementById('crew-type-echart')
      const myPieChart0 = this.$echarts.init(pieChartDom0, null, {
        renderer: 'svg'
      })
      this.crewTypeChart = myPieChart0
      myPieChart0.setOption(this.crewTypeOption)

      // 报修渠道----饼图
      const pieChartDom1 = document.getElementById('repair-channel-echart')
      const myPieChart1 = this.$echarts.init(pieChartDom1, null, {
        renderer: 'svg'
      })
      this.repairChannelChart = myPieChart1
      myPieChart1.setOption(this.repairChannelOption)

      // 满意度评价----饼图
      const pieChartDom2 = document.getElementById('satisfaction-evaluation-echart')
      const myPieChart2 = this.$echarts.init(pieChartDom2, null, {
        renderer: 'svg'
      })
      this.satisfactionEvaluationChart = myPieChart2
      myPieChart2.setOption(this.satisfactionEvaluationOption)
    },
    showFrameworkDetail() {
      this.dialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
.right-content {
  .content-item {
    padding: 12px 20px;
    .el-row.mb16 {
      margin-bottom: 16px;
    }
    .el-row.mb8 {
      margin-bottom: 8px;
    }
    &.content-item__top {
      height: 52%;
      margin-bottom: 16px;
    }
    &.content-item__bottom {
      flex: 1;
    }
  }
}
.content-item__top {
  display: flex;
  flex-direction: column;
  .framework-detail-btn {
    color: #1acdfd;
    cursor: pointer;
    line-height: 20px;
  }
  /deep/ .project-framework-box {
    height: 42%;
    margin-bottom: 16px;
    overflow-x: hidden;
    &:hover {
      overflow-x: auto;
    }
    > div > table {
      padding: 0 16px;
    }
  }
  #crew-type-echart {
    flex: 1;
  }
}
.content-item__bottom {
  display: flex;
  flex-direction: column;
  .workorder-overview-box,
  .workorder-progress-box {
    border: 1px solid rgba(0, 204, 255, 1);
    border-radius: 4px;
    .el-col {
      line-height: 28px;
      text-align: center;
      padding: 2px 0;
      &:not(:last-child) {
        border-right: 1px solid rgba(0, 204, 255, 1);
      }
      div {
        font-size: 28px;
        margin-bottom: 8px;
      }
    }
  }

  .workorder-data-box {
    flex: 1;
    .flexbox {
      display: flex;
      flex-direction: column;
      #repair-channel-echart,
      #satisfaction-evaluation-echart {
        flex: 1;
      }
      &:first-child {
        margin-right: 8px;
      }
    }
  }
}

.framework-detail-dialog {
  background: transparent;
  .framework-detail-box {
    height: calc(100% - 110px);
    overflow-x: hidden;
    padding-bottom: 12px;
    &:hover {
      overflow-x: auto;
    }
  }
  /deep/ .el-dialog {
    width: 80%;
    height: 80%;
    background: linear-gradient(
      180deg,
      rgba(10, 22, 46, 0.8) 0%,
      rgba(16, 36, 71, 0.8) 40%,
      rgba(16, 36, 71, 0.8) 61%,
      rgba(8, 16, 37, 0.8) 100%
    );
    .el-dialog__header {
      border-bottom: 1px solid #1acdfd;
      .el-dialog__title {
        color: #b4e4ff;
        font-weight: 600;
      }
      .el-dialog__headerbtn .el-dialog__close {
        font-size: 24px;
        color: #1acdfd;
      }
    }
    .el-dialog__body {
      color: #1acdfd;
    }
  }
}
</style>



