<!--
 * @Autor: linxu
 * @Date: 2021-12-23 11:46:19
 * @LastEditors: linxu
 * @LastEditTime: 2021-12-31 14:59:39
 * @Description: 架构树组件
-->
<template>
  <div style="line-height: normal;">
    <table>
      <tr>
        <td :colspan="treeData.children ? treeData.children.length * 2 : 1"
            :class="{parentLevel: treeData.children, extend: treeData.children}">
          <div :class="{node: true}">
            <div class="person">
              <div class="name"
                   :class="treeData.level==0?'level':''">
                <p>{{treeData.name}}</p>
                <p class="type"
                   v-if="treeData.type">{{treeData.type}}</p>
              </div>
            </div>
          </div>
        </td>
      </tr>
      <tr v-if="treeData.children && treeData.children.length">
        <td v-for="(children, index) in treeData.children"
            :key="index"
            colspan="2"
            class="childLevel">
          <TreeChart :json="children" />
        </td>
      </tr>
    </table>
  </div>
</template>

<script>

export default {
  name: 'TreeChart',
  inject: ['reload'],
  props: ['json'],
  data () {
    return {
      treeData: {}
    }
  },
  watch: {
    json: {
      handler: function (Props) {
        this.treeData = Props
      },
      immediate: true,
      deep: true
    }
  },
  created () { },
  methods: {
    getdata: function (val) {
      this.$emit('func', val)
    }
  }
}
</script>
<style lang="less" scoped>
table {
  width: 100%;
  margin: 0 auto;
  border-collapse: separate !important;
  border-spacing: 0 !important;
  td {
    position: relative;
    vertical-align: top;
    text-align: center;
    &.parentLevel {
      padding: 0 0 26px 0;
    }
    &:not(:last-child) {
      padding-right: 8px;
    }
  }
}

.extend::after {
  content: "";
  position: absolute;
  right: 50%;
  bottom: 14px;
  height: 13px;
  border-left: 1px solid #0099ff;
  /* transform: translate3d(-1px, 0, 0) */
}

.childLevel::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translate(-4px, 0);
  /* bottom: 100%; */
  top: -15px;
  height: 15px;
  border-left: 1px solid #0099ff;
  // transform: translate3d(-1px, 0, 0);
}

.childLevel::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: -16px;
  border-top: 1px solid #0099ff;
}

.childLevel:first-child:before,
.childLevel:last-child:before {
  display: none;
}

.childLevel:first-child:after {
  left: 50%;
  height: 15px;
  border: 1px solid;
  border-color: #0099ff transparent transparent #0099ff;
  border-radius: 6px 0 0 0;
  // transform: translate3d(1px, 0, 0);
}

.childLevel:last-child:after {
  right: 50%;
  height: 15px;
  border: 1px solid;
  border-color: #0099ff #0099ff transparent transparent;
  border-radius: 0 6px 0 0;
  // transform: translate3d(-1px, 0, 0);
}

.childLevel:first-child.childLevel:last-child::after {
  left: auto;
  border-radius: 0;
  border-color: transparent #0099ff transparent transparent;
  // transform: translate3d(1px, 0, 0);
}

.node {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  text-align: center;
}

.node .person {
  position: relative;
  display: inline-block;
  z-index: 2;
  .name {
    word-break: break-all;
    padding: 4px;
    line-height: 14px;
    border: 1px solid #0099ff;
    border-radius: 4px;
    cursor: pointer;
    height: 48px;
    width: 110px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &.level {
      width: 180px;
    }
    > p {
      line-height: 20px;
    }
  }
}
</style>



