<template>
  <div class="screen-main-center-top">
    <el-row class="top-box" type="flex" align="middle">
      <el-col :span="12">
        <el-row :gutter="5" type="flex" align="middle">
          <el-col :span="8">
            <el-select v-model="itemType" placeholder="请选择项目类型">
              <el-option
                v-for="item in itemTypeOpt"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="8">
            <el-autocomplete
              class="inline-input"
              v-model="companyInput"
              :fetch-suggestions="querySearchCompany"
              :popper-append-to-body="false"
              :debounce="0"
              placeholder="服务企业(可搜索)"
              @select="handleSelectCompany"
            ></el-autocomplete>
          </el-col>
          <el-col :span="8">
            <el-autocomplete
              class="inline-input"
              v-model="contractInput"
              :fetch-suggestions="querySearchContract"
              :popper-append-to-body="false"
              :debounce="0"
              placeholder="合同编号(可搜索)"
              @select="handleSelectContract"
            ></el-autocomplete>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="12" :style="{ display: 'flex', height: '100%' }">
        <div class="reset-box" @click="resetQuery">重置筛选</div>
        <!-- 20211222周三10:16:53lpeng：暂时隐藏【勿删】 -->
        <!-- <div class="filter-box">
          <el-autocomplete
            class="inline-input"
            v-model="searchVal"
            :fetch-suggestions="querySearch"
            placeholder="请输入搜索内容"
            :trigger-on-focus="false"
            @select="handleSelect"
          ></el-autocomplete>
          <div class="icon-box">
            <svg-icon slot="prefix" icon-class="searchIcon" />
          </div>
        </div> -->
      </el-col>
    </el-row>
    <div :style="{ height: 'calc(100% - 40px)' }">
      <e-chart-china-map :mapData="mapData" />
    </div>
  </div>
</template>
<script>
import EChartChinaMap from '../EChartChinaMap'
import _data from '../../data'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: `ScreenMainCenterTop`,
  components: { EChartChinaMap },
  data() {
    return {
      itemType: '',
      company: '',
      companyInput: '',
      contract: '',
      contractInput: '',
      // searchVal: '',
      companyOpt: [],
      contractOpt: [],
      itemTypeOpt: [],
      contractAll: [],
      contractRes: [],
      mapData: {},
      // 年度条件
      year: 0
    }
  },
  mounted() {
    this.getAllContract()
  },
  computed: {
    ...mapGetters(['screenDataConditions', 'selectCrew'])
  },
  watch: {
    screenDataConditions: {
      handler: function (val, oldVal) {},
      deep: true
    },
    itemType(n, o) {
      this.companyInput = ``
      this.company = ``
      this.contractInput = ``
      this.contract = ``
      this.getContractRes()
    },
    company(n, o) {
      this.contractInput = ``
      this.contract = ``
      this.getContractRes()
    },
    contract(n, o) {
      this.getContractRes()
    },
    contractRes(n, o) {
      const conditions = {
        itemType: this.itemType,
        companyId: this.company,
        contract: this.contract,
        year: this.screenDataConditions.year
      }
      // 组装数据给地图
      const mapData = {}
      n.forEach((element) => {
        const mchObj = element.mch
        const contractNum = element.contractNum
        const companyId = element.companyId
        if (conditions.companyId.toString() !== '') {
          conditions.companyId = companyId
        }
        for (const ky in mchObj) {
          mchObj[ky].push(contractNum)
          mchObj[ky].push(companyId)
          mapData[ky] = mchObj[ky]
        }
      })
      this.toggleScreenDataConditons(conditions)
      this.mapData = mapData
    }
  },
  methods: {
    // ** 服务类
    // store
    ...mapActions(['toggleScreenDataConditons', 'toggleSelectCrew']),
    // 服务企业去重
    companyDataHandler(dataArr) {
      const sameSet = new Set()
      return dataArr.filter((item) => {
        if (sameSet.has(item.companyId)) {
          return false
        }
        sameSet.add(item.companyId)
        return true
      })
    },
    // 搜索建议方法
    createFilter(queryString) {
      return (res) => {
        return res.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1
      }
    },
    // ** 业务类
    // 初始化数据，查询所有项目
    getAllContract() {
      // [x] *** 页面已弃用 切换接口数据，切换接口
      this.contractAll = _data.filterData.contract
      this.itemTypeOpt = _data.filterData.itemType
      this.companyOpt = this.companyDataHandler(_data.filterData.contract)
      this.contractOpt = this.contractAll
      this.contractRes = this.contractAll
      // 需要默认选中一个机组，否则进入管理系统后无数据
      if (this.contractAll.length > 0 && this.contractAll[0].mchNum > 0) {
        const mch = this.contractAll[0].mch
        let mchId = 0
        for (const ky in mch) {
          mchId = ky
        }
        this.$api.screenData.getsingleMchInfo({ id: mchId }).then((res) => {
          const data = res.Data
          this.toggleSelectCrew(data)
        })
      }
    },
    // 获取项目结果
    getContractRes() {
      this.contractRes = this.contractAll.filter((item) => {
        const i = this.itemType
        const c = this.company
        const ct = this.contract
        let flag = true
        if ((i === 0 || i) && item.itemType.toString() !== i.toString()) {
          flag = false
        }
        if (c && item.company.toString() !== c.toString()) {
          flag = false
        }
        if (ct && item.contractNum.toString() !== ct.toString()) {
          flag = false
        }

        return flag
      })
      // this.companyOpt = this.companyDataHandler(this.contractRes)
      // this.contractOpt = this.contractRes
    },
    // 重置筛选
    resetQuery() {
      this.itemType = ''
      this.company = ''
      this.companyInput = ''
      this.contractInput = ''
      this.contract = ''
      this.getAllContract()
    },
    // 搜索服务企业
    querySearchCompany(queryString, cb) {
      let newCompanyOpt = this.contractAll.map((item) => {
        item.value = item.company
        return item
      })

      // [x] *** 页面已弃用 拼音
      if (queryString) {
        newCompanyOpt = newCompanyOpt.filter(this.createFilter(queryString))
      }
      if (this.itemType === 0 || this.itemType) {
        newCompanyOpt = newCompanyOpt.filter((item) => {
          if (item.itemType.toString() === this.itemType.toString()) return true
          else return false
        })
      }

      setTimeout(() => {
        cb(this.companyDataHandler(newCompanyOpt))
      }, 0)
    },
    handleSelectCompany(e) {
      this.company = e.value
    },
    // 搜索合同编号
    querySearchContract(queryString, cb) {
      let newContractOpt = this.contractAll.map((item) => {
        item.value = item.contractNum
        return item
      })
      // [x] *** 页面已弃用 拼音
      if (queryString) {
        newContractOpt = newContractOpt.filter(this.createFilter(queryString))
      }
      if (this.itemType === 0 || this.itemType) {
        newContractOpt = newContractOpt.filter((item) => {
          if (item.itemType.toString() === this.itemType.toString()) return true
          else return false
        })
      }
      if (this.company) {
        newContractOpt = newContractOpt.filter((item) => {
          if (item.company.toString() === this.company.toString()) return true
          else return false
        })
      }
      cb(newContractOpt)
    },
    handleSelectContract(e) {
      this.contract = e.value
    }
  }
}
</script>
<style lang='less' scoped>
// 边框颜色
@border-color: #0a6ea8;
// 重置筛选字体颜色
@reset-text-color: #169bd5;
.screen-main-center-top {
  .top-box {
    height: 45px;
    padding: 0 10px;
    background: #092d47;
    border: 1px solid @border-color;
    border-bottom: 0;

    .reset-box {
      width: 100px;
      height: 100%;
      display: flex;
      cursor: pointer;
      align-items: center;
      justify-content: center;
      color: @reset-text-color;
    }
    .filter-box {
      right: 2px;
      width: 200px;
      float: right;
      display: flex;
      position: absolute;
      align-items: center;
      /deep/ input {
        border-right: 0;
      }
    }
    .icon-box {
      width: 70px;
      height: 32px;
      display: flex;
      cursor: pointer;
      border-radius: 5px;
      align-items: center;
      background: #169bd5;
      justify-content: center;
      transform: translateX(-2px);
      svg {
        font-size: 1.5rem;
      }
    }
    // 选择框的指示图标歪了，用此样式修复
    /deep/ .el-input__suffix {
      line-height: 100%;
    }
    // 重写 input 框样式
    /deep/ input {
      color: white;
      border-radius: 0;
      background: transparent;
      border: 1px solid @border-color;
    }
  }
}
</style>



