<template>
  <el-row type="flex" :style="{ height: '100%' }">
    <el-col class="content-item content-item__left">
      <div>机组报警监控</div>
      <!-- <div>机组报警监控{{ screenDataConditions }}</div> -->
      <div>
        <scroll-table
          :column="tableColumn"
          :tableData="tableData"
          @diagnoseHandler="diagnoseHandler"
        >
          <template #operation>
            <div :style="{ flex: 1 }">操作</div>
          </template>
          <template v-slot:operationItem="{ idx }">
            <div class="diagnosis" :style="{ flex: 1, cursor: 'pointer' }">
              <span :style="{ display: 'none' }">
                {{ idx }}
              </span>
              故障诊断
            </div>
          </template>
        </scroll-table>
      </div>
    </el-col>
    <el-col class="content-item content-item__right">
      <div>维保策略提醒</div>
      <div>
        <scroll-table :canStopFlag="true" :column="tableColumnKeep" :tableData="tableDataKeep">
        </scroll-table>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import _data from '../../data'
import ScrollTable from '@/components/ScrollTable'
import { formatTime } from '@/utils/common'
import { mapGetters } from 'vuex'

export default {
  name: `ScreenMainCenterBottom`,
  components: { ScrollTable },
  data() {
    return {
      tableColumn: _data.tableAlert.column,
      tableData: _data.tableAlert.data,
      tableColumnKeep: _data.tableKeep.column,
      tableDataKeep: _data.tableKeep.data
    }
  },
  mounted() {
    this.renderData()
  },
  computed: {
    ...mapGetters(['screenDataConditions'])
  },
  watch: {
    screenDataConditions: {
      handler: function (val, oldVal) {
        this.renderData()
      },
      deep: true
    }
  },
  methods: {
    // 渲染本组件数据
    renderData() {
      this.getAlertData()
      this.getKeepStrategy()
    },
    // 跳转故障曲线页面
    diagnoseHandler(target) {
      if (target && target.classList.contains(`diagnosis`)) {
        this.$router.push('/crewrecord/data-record/alarm-curve-record')
      }
    },
    // 获取机组报警数据
    getAlertData() {
      // [x] *** 页面已弃用 滚动表格测试表格数据，接口调试后修改
      this.tableData = JSON.parse(JSON.stringify(_data.tableAlert.data))
      this.tableData.map((item) => {
        item.time = formatTime(item.time).format('yyyy-MM-DD HH:mm:ss')
        item.itemNum = `T-` + (Math.random() * 10).toFixed(0) + item.mchName.substring(10, 14)
        return item
      })
    },
    // 获取维保策略数据
    getKeepStrategy() {
      // [x] *** 页面已弃用 滚动表格测试表格数据，接口调试后修改
      this.tableDataKeep = _data.tableKeep.data
    }
  }
}
</script>

<style lang="less" scoped>
// 鼠标悬停表格行颜色
@li-hover-color: #183b58;
// 标题文字颜色
@title-text-color: #0099ff;
// 边框样式
.border-style {
  border: 1px solid #0a6fa8;
}

.content-item {
  &.content-item__left {
    margin-right: 8px;
  }
  // margin: 4px 2px 2px 2px;
  & > div:nth-child(1) {
    height: 40px;
    display: flex;
    .border-style();
    border-bottom: 0;
    font-size: 1.1rem;
    font-weight: bold;
    padding: 0 0 0 5px;
    align-items: center;
    justify-content: start;
    color: @title-text-color;
  }
  & > div:nth-child(2) {
    height: calc(100% - 40px);
  }
}
// table style
/deep/ .scroll-table-head {
  color: @title-text-color;
}
/deep/ .diagnosis {
  color: #228b22;
  &:hover {
    color: tomato;
  }
}
/deep/ .scroll-table {
  .border-style();
  box-sizing: border-box;
  .scroll-table-head {
    border-bottom: 1px solid #0a6fa8;
  }
  .scroll-table-body {
    li:hover {
      background: @li-hover-color;
    }
  }
}
</style>



